/* Mobile view */
@media (max-width: 768px){
  .container-fluid,.row,.whoAreWeHeading,.whoAreWeText, .action-btns, .sectionHeading{
    text-align: center;
  }

  .EEIdiv {
    display: none;
  }

  .mobileEEIdiv{
    display: block;
  }

  .EEItext {
    /* Engage • Empower • Innovate */
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    font-family: "Barlow", sans-serif;
    color: #1c307a;
    
    padding-top: 10%;
    width: 100%;
    text-align: center;
  }

  #learnMoreButton {
    margin-left: 0px;
    width: 160px;
    height: 40px;
  }

  #joinNewsletterBtn{
    margin: 20px;
    height: 40px;
  }
}

