.navbar {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  background: #1c307a;
  opacity: 0.9;
  height: 137px;
}

.navbar-navigator {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
  flex: 1 1;
}

.navbar-logo {
  margin-left: 0.5rem;
}

.navbar-logo a {
  padding: 0 10%;
}

.navbar-items-spacer {
  flex: 1 1;
}

.navbar-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar-items li {
  padding: 0 3rem;
}

.navbar-items a {
  color: #abc5ed;
  font-family: "Barlow", sans-serif;
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
}

.navbar-items a:hover,
.navbar-items a:active {
  opacity: 1;
  color: #4761bf;
  text-decoration: none;
}

@media (max-width: 768px){
    .navbar-items{
        display: none;
    }
}
@media (min-width: 769px){
    .sidebar-toggle-button{
        display: none;
    }
    .navbar-logo{
        margin-left: 0;
    }
}


.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 30px;
    width: 36px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus{
    outline: none;
}

.toggle-button-line{
    width: 36px;
    height: 2px;
    background: white;
}
.sidebar{
    height: 100%;
    background: white;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out
}

.sidebar.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.sidebar ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sidebar li{
    margin: 0.5rem 0;
}

.sidebar a{
    color: #1C307A;
    font-family: "Barlow", sans-serif;
    font-size: 24px;
    font-weight: bold;
    text-decoration: none;
}

.sidebar a:hover,
.sidebar a:active{
    color: #f9aa00;
    text-decoration: none;
}

@media (min-width: 769px){
    .sidebar{
        display: none;
    }
}
.pagefade{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
